import { Table, Thead, Tbody, Tr, Th, Td } from 'react-super-responsive-table'
import { isMobile } from 'react-device-detect'
import { useHistory } from 'react-router-dom'
import PropTypes from 'prop-types'

import Button from 'components/Button'
import { dashboardPath } from 'utils/const'

import styles from './suppliers.module.scss'

const Suppliers = ({ supplierBills }) => {
  const history = useHistory()

  const calculateStatistics = () => {
    const results = {
      paid: 0,
      leftToPay: 0,
      sumOfValues: 0,
    }
    supplierBills.forEach((bill) => {
      results.paid += bill.paid
      results.leftToPay += bill.leftToPay
      results.sumOfValues += bill.value
    })
    return results
  }

  const renderFooter = () =>
    isMobile ? (
      <Tbody>
        <Tr>
          <Th style={{ textAlign: 'left' }}>Total:</Th>
          <Th style={{ textAlign: 'right' }}>
            Uplaƒáeno: {calculateStatistics().paid}
          </Th>
          <Th style={{ textAlign: 'right' }}>
            Preostalo: {calculateStatistics().leftToPay}
          </Th>
          <Th style={{ textAlign: 'right' }}>
            Ukupan iznos: {calculateStatistics().sumOfValues}
          </Th>
        </Tr>
      </Tbody>
    ) : (
      <Tbody>
        <Tr>
          <Th />
          <Th />
          <Th style={{ textAlign: 'left' }}>Total:</Th>
          <Th>{calculateStatistics().paid}</Th>
          <Th>{calculateStatistics().leftToPay}</Th>
          <Th>{calculateStatistics().sumOfValues}</Th>
          <Th />
        </Tr>
      </Tbody>
    )

  const renderTableBody = () =>
    supplierBills.map((bill) => (
      <Tr key={bill.id}>
        <Td>{bill.billNumber}</Td>
        <Td>{bill.dateOfCreation}</Td>
        <Td>{bill.supplierName}</Td>
        <Td>{bill.paid}</Td>
        <Td>{bill.leftToPay}</Td>
        <Td>{bill.value}</Td>
        <Td className={!isMobile && styles.actionsWrapper}>
          <Button
            label="Pogledaj raƒçun"
            className="btn-purple-outline"
            onClick={() =>
              history.push(dashboardPath.editSupplier.replace(':id', bill.id))
            }
          />
        </Td>
      </Tr>
    ))

  return (
    <>
      <div className={styles.buttonWrapper}>
        <Button
          label="Dodaj novi raƒçun"
          className="btn-purple-outline"
          onClick={() => history.push(dashboardPath.addSupplier)}
        />
      </div>
      <Table className={styles.table}>
        <Thead>
          <Tr>
            <Th>Broj raƒçuna</Th>
            <Th>Datum</Th>
            <Th>Ime dobavljaƒça</Th>
            <Th>Uplaƒáeno</Th>
            <Th>Preostalo</Th>
            <Th>Ukupan iznos</Th>
            <Th />
          </Tr>
        </Thead>
        <Tbody>{renderTableBody()}</Tbody>
        {renderFooter()}
      </Table>
    </>
  )
}

Suppliers.propTypes = {
  supplierBills: PropTypes.arrayOf(
    PropTypes.shape({
      billNumber: PropTypes.string.isRequired,
      dateOfCreation: PropTypes.string.isRequired,
      id: PropTypes.string.isRequired,
      leftToPay: PropTypes.number.isRequired,
      paid: PropTypes.number.isRequired,
      value: PropTypes.number.isRequired,
      supplierName: PropTypes.string.isRequired,
      transactions: PropTypes.arrayOf(
        PropTypes.shape({
          id: PropTypes.string.isRequired,
          billID: PropTypes.string.isRequired,
          creationDate: PropTypes.string.isRequired,
          value: PropTypes.number.isRequired,
        }),
      ).isRequired,
      userId: PropTypes.string.isRequired,
    }),
  ),
}

Suppliers.defaultProps = {
  supplierBills: [],
}

export default Suppliers
